const translateCity = (city: string) => {
  let result: string;

  switch (city) {
    case 'Taipei&NewTaipei':
      result = '  ';
      break;
    case 'Taipei':
      result = '';
      break;
    case 'NewTaipei':
      result = '';
      break;
    case 'Taoyuan':
      result = '';
      break;
    case 'Taichung':
      result = '';
      break;
    case 'Tainan':
      result = '';
      break;
    case 'Kaohsiung':
      result = '';
      break;
    case 'Other_City':
      result = '';
      break;
    default:
      result = city;
  }

  return result;
};

export default translateCity;
